.form-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  margin: 10vh auto;
  padding: 2rem;
  width: 50%;
  height: 70vh;

  .logo2{
    width: 20rem;
  }

}

.login{

  *{
    display: block;
  }

  input[type="email"],
  input[type="password"]{
    padding: .5rem;
    font-size: 2rem;
    outline: none;
  }

  input[type="submit"]{
    margin-left: auto;
    margin-right: auto;
  }

  label{
    font-size: 2rem;
  }
}
