.modal{
  
  .modal-title{
    font-size: 2rem;
    font-weight: bold;
  }
  
  .modal-dialog{
    max-width: 60rem;

    .modal-content{
      padding: 1rem;
    }
  }

  .modal-footer > *{
    padding: .8rem 1.5rem;
    font-size: 1.5rem;
  }

  .modal-footer > *:last-child{
    background-color: #d6d6d6;
  }
}

.modal-body{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  column-gap: 2rem;
  
  div{
    margin-bottom: 1rem;
  }

  label:not(label[for="servicio"]),
  input:not(input[type="checkbox"]),
  textarea{
    display: block;
    width: 100%;
    outline: none;
    resize: none;
    font-size: 2rem;
  }

  input[type="date"],
  .hora-llegada > input,
  .hora-salida > input{
    height: 3rem;
  }

  label{
    text-transform: uppercase;
  }

  .servicio{
    display: flex;
    flex-direction: column;
  }

}

.pais,
.posada,
.servicios,
.observacion{
  grid-column: span 2;
}