.inicio{
  width: 100%;
  text-transform: uppercase;

  aside{
    display: flex;
    gap: 3rem;
    padding: 0 2rem;
    margin-left: 2rem;

    a{
      margin-bottom: 2rem;
    }
  }

  .bienvenida{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 0 0 2rem;
    height: 50rem;

    h1{
      text-align: center;
      font-size: 5rem;
      font-weight: $regular;
    }
    
  }
}