.principal{
  display: flex;
  padding: 2rem;
  margin-bottom: 0;

  nav{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    a{
      margin-left: 2rem;
      margin-right: 2rem;
    }

    a:last-of-type{
      margin-bottom: 3rem;
    }
  }

}

.principal nav,
.inicio aside,
.bienvenida{
  border: 1.5px solid #000;
}

